import Modal, { useModal } from '@jdthornton/animated-modal';
import Layout from '../Layout';
import { ModalPropMenu } from '../Modal';
import usePropState from '../../hooks/usePropState';
import '@jdthornton/animated-modal/styles.css';
import styles from './index.module.css';

const initialProps = {
  showCloseButton: true,
  closeOnEsc: true,
  closeOnOverlayClick: true,
  Text: "This modal is now open."
}

const App = () => {
  const { isOpen, close, open } = useModal();
  const [ state, handleChange ] = usePropState(initialProps);
  return(
    <Layout
      title="@jdthornton/animated-modal"
      npm="https://www.npmjs.com/package/@jdthornton/animated-modal"
      github="https://github.com/jdthornton/animated-modal"
      menu={<ModalPropMenu state={state} onChange={handleChange} />}>
      <button type="button" onClick={open}>Open Modal</button>
      <Modal isOpen={isOpen} className={styles.modal} close={close} {...state}>
        {state.Text}
      </Modal>
    </Layout>
  )
}

export default App;
