import { ToastProvider, Toaster, useAddToast } from '@jdthornton/toast';
import Layout from '../Layout';
import PropField, { BoolPropField } from '../PropField';
import usePropState from '../../hooks/usePropState';
import '@jdthornton/toast/styles.css';

const initialProps = {
  delay: 4000,
  Text: "This toast is for you"
}

const App = () => {
  const [ state, handleChange ] = usePropState(initialProps);
  const addToast = useAddToast();
  return(
    <Layout
      title="@jdthornton/toast"
      npm="https://www.npmjs.com/package/@jdthornton/toast"
      github="https://github.com/jdthornton/toast"
      menu={(<>
        <PropField name="delay">
          <input type="number" id="delay" name="delay" value={state.delay} onChange={handleChange} />
        </PropField>
        <PropField name="Text">
          <textarea id="Text" name="Text" value={state.Text} onChange={handleChange} />
        </PropField>
      </>)}>
      <button type="button" onClick={() => addToast(state.Text,state.delay ? state.delay : 4000)}>
        Add Toast
      </button>
    </Layout>
  )
}

const AppContainer = () => (
  <ToastProvider>
    <App />
    <Toaster />
  </ToastProvider>
)

export default AppContainer;
