import Modal, { useModal } from '@jdthornton/modal';
import Layout from '../Layout';
import PropField, { BoolPropField } from '../PropField';
import usePropState from '../../hooks/usePropState';
import '@jdthornton/modal/styles.css';
import styles from './index.module.css';

const initialProps = {
  showCloseButton: true,
  closeOnEsc: true,
  closeOnOverlayClick: true,
  Text: "This modal is now open."
}

export const ModalPropMenu = ({state,onChange}: any) => (<>
  <BoolPropField name="showCloseButton" value={state.showCloseButton} onChange={onChange} />
  <BoolPropField name="closeOnEsc" value={state.closeOnEsc} onChange={onChange} />
  <BoolPropField name="closeOnOverlayClick" value={state.closeOnOverlayClick} onChange={onChange} />
  <PropField name="Text">
    <textarea id="Text" name="Text" value={state.Text} onChange={onChange} />
  </PropField>
</>)

const App = () => {
  const { isOpen, close, open } = useModal();
  const [ state, handleChange ] = usePropState(initialProps);
  return(
    <Layout
      title="@jdthornton/modal"
      npm="https://www.npmjs.com/package/@jdthornton/modal"
      github="https://github.com/jdthornton/modal"
      menu={<ModalPropMenu state={state} onChange={handleChange} />}>
      <button type="button" onClick={open}>Open Modal</button>
      {isOpen && (
        <Modal className={styles.modal} close={close} {...state}>
          {state.Text}
        </Modal>
      )}
    </Layout>
  )
}

export default App;
