import styles from './index.module.css';
import githubIcon from '../../assets/images/GitHub-Mark-32px.png'
import npmIcon from '../../assets/images/n-64.png'

const Menu = ({children,title,github,npm}: any) => (
  <aside className="App__controls">
    <div className={styles.header}>
      <div className={styles.title}>{title}</div>
      <div className={styles.links}>
        {github && <a href={github} target="_blank"><img src={githubIcon} className={styles.icon} /></a>}
        {npm && <a href={npm} target="_blank"><img src={npmIcon} className={styles.icon} /></a>}
      </div>
    </div>
    {children}
  </aside>
)

export default Menu;
